import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import App from "./App";
import ErrorPage from "./ErrorPage";
const Routing = () => {
  return (
    <>
      <Router>
        <Switch>
          <Route exact path="/" component={App} />
          <Route component={ErrorPage} />
        </Switch>
      </Router>
    </>
  );
};
export default Routing;
